<template>
  <div id="app">
    <div class="loading" v-show="loadinprocess">
      <span class="loader"></span>
    </div>
    <nav class="navbar navbar-expand-sm bg_app navbar-dark">
      <div class="container-fluid">
        <a class="navbar-brand" href="#">Vue App Order Item</a>
        <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#collapsibleNavbar">
          <span class="navbar-toggler-icon"></span>
        </button>
        <div class="collapse navbar-collapse" id="collapsibleNavbar">

          <ul class="navbar-nav ms-auto mb-2 mb-lg-0">
            <li class="nav-item dropdown">
              <a class="nav-link dropdown-toggle text-white" href="#" role="button" data-bs-toggle="dropdown">VueApp</a>
              <ul class="dropdown-menu">
                <li><a class="dropdown-item" href="#">Logout</a></li>
              </ul>
            </li>
          </ul>
        </div>
      </div>
    </nav>

    <div class="container-fluid mt-3">
      <div class="mt-4">
        <div class="row">
          <div class="col-md-8">
            <div class="row">
              <div class="col-md-6">
                <div class="form-group">
                  <label for="category">Categories</label>
                  <select class="form-select" v-on:change="getProductByCategory">
                    <option value="all">All Categories</option>
                    <option v-for="(item, i) in category" :key="i" :value="item.id">
                      {{ item.title }}
                    </option>
                  </select>
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="category">Table</label>
                  <select class="form-select" v-on:change="selectTable" v-model="table_id">
                    <option value="table">---Select Table----</option>
                    <option v-for="(item, i) in table" :key="i" :value="item.id">
                      {{ item.title }}
                    </option>
                  </select>
                </div>
              </div>
            </div>
            <div style="height:80vh; overflow-x: hidden;">
              <div class="row mt-2">
                <div class="col-md-3 mb-1" v-for="(pro, i) in product" :key="i">
                  <div style="cursor: pointer;">
                    <div class="card " style="height: 150px;" v-on:click="showModal(pro.id)">
                      <div class="card-body text-center card_item" style="position: relative;">
                        <span class="card_item_type">{{ pro.types }}</span>
                        <img :src="pro.images" alt="image" width="80">
                        <div class="mt-2">
                          {{ pro.title }}
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="col-md-4">
            <div style="height: 75vh;">
              <div class="table-responsive">
                <table class="table">
                  <thead>
                    <tr>
                      <th class="table_header" width="160">Product</th>
                      <th class="table_header" width="20">Qty</th>
                      <th class="table_header" width="90">Price</th>
                      <th class="table_header" width="90">Total $</th>
                      <th class="table_header" width="20"></th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(item, i) in product_order" :key="i">
                      <td>
                        <div class="item_order">
                          <div><strong>{{ item.product_title }}</strong></div>
                          <span style="font-size: 1rem;" class="text_color">{{ item.pro_unit_title
                          }}</span>
                        </div>
                      </td>
                      <td>{{ item.product_qty }}</td>
                      <td>{{ item.pro_unit_price }}</td>
                      <td>{{ item.pro_unit_total_price }}</td>
                      <td>
                        <button class="btn btn-danger btn-sm" v-on:click="removeItem(item.product_id)">
                          <i class="fa fa-trash" aria-hidden="true"></i>
                        </button>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
            <div>
              <div class="summary_footer bg_app">
                <div style="display: flex; justify-content: space-between;">
                  <div>Tax %</div>
                  <div>
                    <h5>
                      00
                    </h5>
                  </div>
                </div>
                <div style="display: flex; justify-content: space-between;">
                  <div>Discount %</div>
                  <div>
                    <h5>
                      00
                    </h5>
                  </div>
                </div>
                <div style="display: flex; justify-content: space-between;">
                  <div>Total Price $</div>
                  <div>
                    <h5>{{ total_amount }}</h5>
                  </div>
                </div>
              </div>
              <div class="mt-2" style="float: right;">
                <button type="button" class="btn btn-primary me-2" v-on:click="saveOrder" :disabled="visible_save">
                  <i class="fa fa-floppy-o" aria-hidden="true"></i>
                  Save Order
                </button>
                <button type="button" class="btn btn-primary" v-on:click="orderPaymnt" :disabled="visible_paymnet">
                  <i class="fa fa-money" aria-hidden="true"></i>
                  Payment
                </button>
              </div>
            </div>
          </div>
        </div>
        <b-modal ref="my-modal" hide-footer title="Order Type">
          <div class="d-block">
            <div style="position: relative; left: 40%;" v-show="loading_unit">Loading</div>
            <div v-show="loadingUnit_item" class="form-check" v-for="(item, i) in unit" :key="i">
              <div class="form-check">
                <div v-if="item.default == 1">
                  <input type="radio" v-model="pro_unit_data" class="form-check-input" :id="item.id" name="optradio"
                    :value="item.unit_id + ',' + item.price + ',' + item.unit_title" checked>
                  {{ item.unit_title }} - {{ item.price }} $
                  <label class="form-check-label" :for="item.id"></label>
                </div>
                <div v-else>
                  <input type="radio" v-model="pro_unit_data" class="form-check-input" :id="item.id" name="optradio"
                    :value="item.unit_id + ',' + item.price + ',' + item.unit_title">
                  {{ item.unit_title }} - {{ item.price }} $
                  <label class="form-check-label" :for="item.id"></label>
                </div>
              </div>
            </div>
            <div class="form-group mt-2">
              <label for="unit_qty">Qty</label>
              <input type="number" class="form-control" v-model="product_qty">
            </div>
          </div>
          <button style="float: right;" type="button" class="btn btn-primary mt-2" v-on:click="hideModal">Add</button>
        </b-modal>
        <b-modal ref="payment_modal" hide-footer title="Payment">
          <div class="d-block">
            <div class="form-group">
              <label for="deposit">Deposit</label>
              <input type="number" class="form-control" v-model="amount_deposit" v-on:keyup="calDeposit">
            </div>
            <div class="form-group mt-4">
              <label for="deposit">Amount to be paid</label>
              <input type="text" class="form-control" v-model="total_amount" disabled>
            </div>
            <div class="form-group mt-4">
              <label for="deposit">Return Amount</label>
              <input type="text" class="form-control" v-model="amount_return" disabled>
            </div>
          </div>
          <div class="d-grid mt-4">
            <button type="button" class="btn btn-primary btn-block" v-on:click="paymentOrderfinal">Payment</button>
          </div>
        </b-modal>

        <Invoices :invoice_data="product_order" :invoice_table="invoice_table" :invoice_number="invoice_number"
          :invoice_date="invoice_date" :total_amount="total_amount" />
      </div>
    </div>
  </div>
</template>


<script >
import ApiService from "./service";
import InvoiceComponent from "./components/invoice.vue";
export default {
  name: 'NNdf',
  data() {
    return {
      loadingUnit_item :false,
      loading_unit :true,
      loadinprocess: true,
      visible_save: true,
      visible_paymnet: true,
      invoice_number: "",
      invoice_date: "",
      invoice_table: "",
      amount_deposit: "0",
      amount_paid: "00",
      amount_return: "00",
      category: [],
      table: [],
      sale_date: "",
      product: [],
      product_order: [],
      unit: [],
      table_id: "table",
      product_id: "",
      product_title: "",
      product_qty: 1,
      pro_unit_title: "",
      pro_unit_qty: 1,
      pro_unit_data: "",
      total_amount: "00",
      disount: '0'

    };
  },
  components: {
    "Invoices": InvoiceComponent
  },
  methods: {
    saveOrder() {

      var app = this;
      this.loadinprocess = true;
      var sales = {
        "table_id": app.table_id,
        "sale_date": app.sale_date,
        "total_amount": this.total_amount,
        "deposit": 0,
        "amount_paid": 0,
        "return_amount": 0
      }
      ApiService.saveOrder(sales)
        .then((response) => {
          for (var i = 0; i < app.product_order.length; i++) {
            var dataPayment = {
              "sale_id": response.data.id,
              "product_id": app.product_order[i].product_id,
              "unit_id": app.product_order[i].pro_unit_id,
              "qty": app.product_order[i].product_qty,
              "price": app.product_order[i].pro_unit_price
            };
            ApiService.saveOrderData(dataPayment)
              .then((response) => {
                console.log(response.data);
                this.loadinprocess = false;
                window.location.reload(true);
              })
              .catch((e) => {
                console.log(e);
              });
          }
          
        })
        .catch((e) => {
          console.log(e);
        });
    },
    selectTable() {
      
      var app = this;
      app.loadinprocess = true;
      app.product_order = []
      ApiService.getSelectTable(this.table_id)
        .then((res) => {
          this.loadinprocess = false;
          if (res.data == null || res.data == "") {
            app.product_order = []
            app.total_amount = 0;
            this.visible_save = true;
            this.visible_paymnet = true;
            
          } else {
            for (var i = 0; i < res.data.length; i++) {
              var rowtable = res.data[i].sale_detail;
              for (var j = 0; j < rowtable.length; j++) {
                var listOrder = {
                  product_id: rowtable[j].product_id,
                  product_title: rowtable[j].product_title,
                  product_qty: rowtable[j].qty,
                  pro_unit_id: rowtable[j].unit_id,
                  pro_unit_title: rowtable[j].pro_unit_title,
                  pro_unit_price: rowtable[j].price,
                  pro_unit_total_price: (rowtable[j].qty * rowtable[j].price).toFixed(2),
                }
                app.product_order.push(listOrder);
              }
            }
            this.visible_save = false;
            this.visible_paymnet = false;
            this.updateLastTotal();
          }
        })
        .catch((e) => {
          console.log(e);
        });
    },
    removeItem(id) {
      const index = this.product_order.findIndex(x => x.product_id == id);
      this.product_order.splice(index, 1);
      this.updateLastTotal();
    },
    updateLastTotal() {
      var app = this;
      var tot = 0;
      for (var i = 0; i < app.product_order.length; i++) {
        tot += Number(app.product_order[i].pro_unit_price * app.product_order[i].product_qty);
      }
      app.total_amount = Number(tot).toFixed(2);
    },
    showModal(id) {
      this.loading_unit = true;
      this.loadingUnit_item = false;
      if (this.table_id == "table") {
        this.$toastr.e("Please Select Table first !!");
      } else {
        this.$refs['my-modal'].show();
        ApiService.getUnit(id)
          .then((response) => {
           
            var unitItem = response.data.product_unit;
            var prodItem = response.data.product;
            this.unit = unitItem;
            for (var j = 0; j < unitItem.length; j++) {
              if (unitItem[j].default == 1) {
                this.pro_unit_data = unitItem[j].unit_id + ',' + unitItem[j].price + ',' + unitItem[j].unit_title;
              }
            }
            for (var h = 0; h < prodItem.length; h++) {
              this.product_title = prodItem[h].title;
              this.product_id = prodItem[h].product_id;
            }
            this.loading_unit = false;
            this.loadingUnit_item = true;
          })
          .catch((e) => {
            console.log(e);
          });
      }
    },
    hideModal() {
      var sups = this.pro_unit_data.split(",");
      var index = this.product_order.findIndex((item) => item.product_id == this.product_id && item.pro_unit_id == sups[0]);
      if (index != -1) {
        var qtyd = parseInt(this.product_order[index].product_qty);
        this.product_order[index].product_qty = qtyd + parseInt(this.product_qty);
        this.product_order[index].pro_unit_total_price = (this.product_order[index].pro_unit_price * this.product_order[index].product_qty).toFixed(2);

      } else {
        var data = {
          product_id: this.product_id,
          product_title: this.product_title,
          product_qty: this.product_qty,
          pro_unit_id: sups[0],
          pro_unit_title: sups[2],
          pro_unit_price: sups[1],
          pro_unit_total_price: Number(sups[1] * this.product_qty).toFixed(2),
        }
        this.product_order.push(data);
      }
      this.updateLastTotal();
      this.product_qty = 1;
      this.visible_save = false;
      this.visible_paymnet = true
      this.$refs['my-modal'].hide();

    },
    getProductByCategory(e) {
      this.loadinprocess = true;
      ApiService.getByCategory(e.target.value)
        .then((response) => {
          this.product = response.data;
          this.loadinprocess = false;
        })
        .catch((e) => {
          console.log(e);
          this.loadinprocess = false;
        });
    },
    retrieveData() {
      ApiService.get()
        .then((response) => {
          this.category = response.data.category;
          this.table = response.data.table;
          this.product = response.data.product;
          this.loadinprocess = false;
        })
        .catch((e) => {
          console.log(e);
        });
    },
    calDeposit() {
      if (this.amount_deposit == "") {
        this.amount_return = "00";
      } else {
        this.amount_return = (this.amount_deposit - this.total_amount).toFixed(2);
      }
    },
    orderPaymnt() {
      ApiService.paymentOrderInfor(this.table_id)
        .then((response) => {
          for (var w = 0; w < response.data.length; w++) {
            this.invoice_number = "INV " + response.data[w].sales_number;
            this.invoice_date = ": " + response.data[w].sale_date;
            this.invoice_table = ": " + response.data[w].table_title;
          }
        })
        .catch((e) => {
          console.log(e);
        });
      this.$refs['payment_modal'].show()
    },
    paymentOrderfinal() {
      var app = this;
      app.loadinprocess = true;
      var sales = {
        "table_id": app.table_id,
        "total_amount": this.total_amount,
        "deposit": this.amount_deposit,
        "amount_paid": this.total_amount,
        "return_amount": this.amount_return
      }
      ApiService.paymentOrder(sales)
        .then((response) => {
          this.loadinprocess = false;
          this.invouce_number = response.data.sales_number;
          const printContents = document.getElementById('divToPrint').innerHTML;
          const originalContents = document.body.innerHTML;
          document.body.innerHTML = printContents;
          window.print();
          document.body.innerHTML = originalContents;
        })
        .catch((e) => {
          console.log(e);
        });
      window.onafterprint = function () {
        window.location.reload(true);
      };
    }
  },
  mounted() {
    this.retrieveData();
    const today = new Date();
    this.sale_date = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate();
  },
}
</script>