import axios from "axios";

class ApiService {
	get() {
		return axios.get("restaurant");
	}
	getByCategory(id) {
		return axios.get("restaurant/procategories/"+id);
	}
	getUnit(id) {
		return axios.get("restaurant/unit/"+id);
	}
  
	saveOrder(data) {
		return axios.post("restaurant/saveorder", data);
	}
	saveOrderData(data) {
		return axios.post("restaurant/saveorderdetail", data);
	}
	getSelectTable(id) {
		return axios.get("restaurant/changetable/"+id);
	}
	paymentOrder(data) {
		return axios.post("restaurant/paymentorder",data);
	}
	paymentOrderInfor(id) {
		return axios.get("restaurant/paymentorderinfor/"+id);
	}
}

export default new ApiService();